<template>
  <div>
    <div>
      <div v-for="i in sjj">
        <div v-if="sjj">
          <div style="overflow:hidden;margin: 0.3rem;">
            <img
              style="width:0.65rem;height:0.65rem;border-radius: 50%;float:left;margin-right:0.3rem;"
              :src="i.header"
              alt
            />
            <p
              v-if="sjj"
              style="line-height:0.65rem;font-size:0.26rem;height:0.65rem;float:left;"
            >{{i.username}}</p>
            <p
              class="iconfont"
              style="line-height:0.65rem;font-size:0.26rem;height:0.65rem;float:right;"
            >&#xe6b4;</p>
          </div>
          <div style="height:4.2rem;" v-if="i.video">
            <video :src="i.video" controls :poster="i.thumbnail" width="100%" height="100%"></video>
          </div>
          <div style="width:100%;" v-else-if="i.img">
            <img :src="i.img" alt />
          </div>
          <div v-else-if="i.text">
            <p style="line-height:0.6rem;font-size:0.26rem;min-height:0.6rem;">{{i.text}}</p>
          </div>
          <div style="overflow:hidden;width:100%;text-align:center;padding:0.3rem 0;">
            <p style="font-size:0.26rem;float:left;width:25%;lin-height:0.68rem;" @click="ccv()"  :style="{color:type?'#d2342f':''}">
              <span class="iconfont" style="font-size:0.26rem;">&#xe627;</span>
              {{num}}
            </p>
            <p style="font-size:0.26rem;float:left;width:25%;lin-height:0.68rem;" @click="ccb" :style="{color:type2?'#d2342f':''}">
              <span class="iconfont" style="font-size:0.26rem;">&#xe601;</span>
              
            </p>
            <p style="font-size:0.26rem;float:left;width:25%;lin-height:0.68rem;" @click="ccb" :style="{color:type2?'#d2342f':''}">
              <span class="iconfont" style="font-size:0.26rem;">&#xe67e;</span>
            </p>
            <p style="font-size:0.26rem;float:left;width:25%;lin-height:0.68rem;" @click="ccb" :style="{color:type2?'#d2342f':''}">
              <span class="iconfont" style="font-size:0.26rem;">&#xe61c;</span>
            </p>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      sjj: "",
      num: 22,
      type: false,
      type2: false
    };
  },
  methods: {
    ccv() {
        this.type=!this.type
      if (!this.type) {
        this.num--
      } else {
        this.num++;
      }
    },
    ccb(){
        this.type2=!this.type2
    }
  },
  created() {
    this.$http
      .get("https://www.apiopen.top/satinGodApi", {
        params: {
          type: 1,
          page: 1
        }
      })
      .then(
        data => {
          console.log(data.body.data);
          this.sjj = data.body.data;
        },
        err => {}
      );
  }
};
</script>
<style>
.mint-tabbar > .mint-tab-item.is-selected {
  font-size: 0.32rem;
}
</style>


