<template>
  <div>
    <div v-for="i in sjj2">
      <div style="overflow:hidden;margin: 0.3rem;">
        <img
          style="width:0.65rem;height:0.65rem;border-radius: 50%;float:left;margin-right:0.3rem;"
          :src="i.header"
          alt
        />
        <p style="line-height:0.65rem;font-size:0.26rem;height:0.65rem;float:left;">{{i.username}}</p>
        <p
          class="iconfont"
          style="line-height:0.65rem;font-size:0.26rem;height:0.65rem;float:right;"
        >&#xe6b4;</p>
        <div style="width:100%;">
          <p
            v-if="i.text"
            style="line-height:0.6rem;font-size:0.26rem;min-height:0.6rem;"
          >{{i.text}}</p>

          <div @click="bbt()" style="max-height:4.5rem;min-width:100%;overflow:hidden;">
            <img :src="i.image" alt />
          </div>
        </div>
        <div style="overflow:hidden;width:100%;text-align:center;padding:0.3rem 0;">
          <p
            style="font-size:0.26rem;float:left;width:25%;lin-height:0.68rem;"
            @click="ccv()"
            :style="{color:type?'#d2342f':''}"
          >
            <span class="iconfont" style="font-size:0.26rem;">&#xe627;</span>
            {{num}}
          </p>
          <p
            style="font-size:0.26rem;float:left;width:25%;lin-height:0.68rem;"
            @click="ccb"
            :style="{color:type2?'#d2342f':''}"
          >
            <span class="iconfont" style="font-size:0.26rem;">&#xe601;</span>
          </p>
          <p
            style="font-size:0.26rem;float:left;width:25%;lin-height:0.68rem;"
            @click="ccb"
            :style="{color:type2?'#d2342f':''}"
          >
            <span class="iconfont" style="font-size:0.26rem;">&#xe67e;</span>
          </p>
          <p
            style="font-size:0.26rem;float:left;width:25%;lin-height:0.68rem;"
            @click="ccb"
            :style="{color:type2?'#d2342f':''}"
          >
            <span class="iconfont" style="font-size:0.26rem;">&#xe61c;</span>
          </p>
        </div>
      </div>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      sjj2: "",
      type: false,
      type2: false,
       num: 22,
    };
  },
  methods: {
    bbt() {
      $(".xs").show();
    },
    bbt2(){
        $(".xs").hide();
    },
    ccv() {
        this.type=!this.type
      if (!this.type) {
        this.num--
      } else {
        this.num++;
      }
    },
    ccb(){
        this.type2=!this.type2
    }
  },
  created() {
    this.$http
      .get("https://www.apiopen.top/satinGodApi", {
        params: {
          type: 3,
          page: 1
        }
      })
      .then(
        data => {
          console.log(data.body.data);
          this.sjj2 = data.body.data;
        },
        err => {}
      );
  }
};
</script>
<style>
.xs{
    position: absolute;
    display: none;
    left: 0;
    top: 0;
    text-align: center;
    width: 100%;
}
.xs img{
    transform: translateY(-50%);
}

</style>


