<template>
  <div>
    <mt-tabbar class="aa1" style="dispaly:block;width:7.5rem;height:0.68rem;position:absolute;top:0;overflow-x:auto;" v-model="selected">
      <mt-tab-item v-for="i in arr" :id="i.id">
        {{i.text}}
      </mt-tab-item>
      <div style="height:0.68rem;width:0.68rem;background:rgba(255,255,255,.6);position:absolute; right:0;z-index:5;">
          <p class="iconfont" style="font-size:0.28rem;line-height:0.68rem;">&#xe517;</p>
      </div>
    </mt-tabbar>
    <div style="margin-top:0.68rem;padding:0.3rem 0;">
        <router-view></router-view>
    </div>
  </div>
</template>
<script>
import { Tabbar, TabItem } from "mint-ui";
import { Indicator } from "mint-ui";
export default {
    mounted() {
    // Indicator.open();

    Indicator.close();
  },
  watch: {
    selected(a, b) {
      location.href = a;
    }
  },
  created() {
    this.selected = "#" + this.$route.path;
    
  },
  data() {
    return {
        selected: "#/sy/tui",
      arr:[
          {
              text:'关注',
              id:'#/sy/guan'
          },
          {
              text:'推荐',
              id:'#/sy/tui'
          },
          {
              text:'图片',
              id:'#/sy/tp'
          },
          {
              text:'文字',
              id:'#/sy/wz'
          },
          {
              text:'小说',
              id:'#/sy/xs'
          }
      ]
    };
  }
};
</script>
<style>
.aa1{
    height: 0.68rem;
}
.aa1 .mint-tab-item-label{
font-size: 0.28rem;
line-height: 0.68rem;
}
</style>


