import Vue from 'vue'
import Router from 'vue-router'
import resource from 'vue-resource'

import sy from './com/sy.vue'
import fx from './com/fx.vue'
import ja from './com/ja.vue'
import xx from './com/xx.vue'
import wd from './com/wd.vue'

import tui from './more/tui.vue'
import guan from './more/guan.vue'
import tp from './more/tp.vue'
import xs from './more/xs.vue'
import wz from './more/wz.vue'
import xiang from './more/xiang.vue'

Vue.use(Router)
Vue.use(resource)

var routes = [
    {
        path: '/sy',
        name: 'sy',
        component: sy,
        children: [
            {
                path: 'tui',
                name: 'tui',
                component: tui,
            },
            {
                path:'xiang/:uid',
                component:xiang
            },
            {
                path: 'guan',
                name: 'guan',
                component: guan,
            },
            {
                path: 'tp',
                name: 'tp',
                component: tp,
            },
            {
                path: 'wz',
                name: 'wz',
                component: wz,
            },
            {
                path: 'xs',
                name: 'xs',
                component: xs,
            },
        ]
    },
    {
        path: '/fx',
        name: 'fx',
        component: fx
    },
    {
        path: '/ja',
        name: 'ja',
        component: ja
    },
    {
        path: '/xx',
        name: 'xx',
        component: xx
    },
    {
        path: '/wd',
        name: 'wd',
        component: wd
    },
]
export default new Router({
    routes
})


