<template>
  <mt-tabbar class="aa" style="height:1rem;" v-model="selected">
    <mt-tab-item class="wenz" id="#/sy/tui">
      <p class="iconfont" slot="icon">&#xe63f;</p>
      <p>首页</p>
    </mt-tab-item>
    <mt-tab-item class="wenz" id="#/fx">
      <p class="iconfont" slot="icon">&#xe705;</p>
      <p>发现</p>
    </mt-tab-item>
    <mt-tab-item class="wenz" id="#/ja" style="font-size:1rem;line-height:0;">
      +
    </mt-tab-item>
    <mt-tab-item class="wenz" id="#/xx">
      <p slot="icon" class="iconfont">&#xe501;</p>
      <p>消息</p>
    </mt-tab-item>
    <mt-tab-item class="wenz" id="#/wd">
      <p class="iconfont" slot="icon">&#xe60f;</p>
      <p>我的</p>
    </mt-tab-item>
  </mt-tabbar>
</template>
<script>
import { Tabbar, TabItem } from "mint-ui";
import { Indicator } from "mint-ui";
export default {
  data() {
    return {
      selected: "#/sy/tui"
    };
  },
  mounted() {
    // Indicator.open();

    Indicator.close();
  },
  watch: {
    selected(a, b) {
      location.href = a;
    }
  },
  created() {
    this.selected = "#" + this.$route.path;
  }
};
</script>
<style>
.aa {
  position: fixed;
}
.aa .mint-tab-item-label {
  font-size: 0.28rem;
  line-height: 0rem;
}
.aa .mint-tab-item-icon .iconfont {
  font-size: 0.28rem;
}
.aa .mint-tab-item-icon {
  height: 0.5rem;
}
.aa .mint-tab-item-icon {
  padding-top: 0.06rem;
}
.aa .wenz:nth-of-type(3) .mint-tab-item-label{
    font-size: 0.8rem;
    box-sizing: border-box;
    line-height: 0.7rem;
    height: 0.8rem;
    width: 0.8rem;
    margin: 0.1rem 0.1rem 0.1rem 0.3rem;
    border-radius: 50%;
    background: #fe5782;
    color: #fff;
}
</style>


